.single__cards__item__link {
  background: #EFEBD8;
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
  margin-bottom: 10px;
  margin-top: 10px;
  margin-left: 15%;
  margin-right: 15%;
}

.single__cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 60%;
  overflow: hidden;
}

.single__fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.single__cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 20px;
  font-weight: 700;
  color: #EFEBD8;
  background-color: #960018;
  box-sizing: border-box;
  border-radius: 8px;
}

.single__cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.single__cards__item__img:hover {
  transform: scale(1.1);
}

.single__cards__item__info {
  padding: 20px 30px 30px;
}

